<template>
  <div>
    <!--       
      modal_opacity  .-->
    <!--    modal_frame,
         .  -->
    <div v-bind:class="{ cafe_modal_frame_big: true }">
      <!--     modal_inner,     
       .-->
      <div class="cafe_modal_inner">
        <div class="cafe_modal_title_inner">
          <span class="modal_title"></span>
        </div>
        <div>
          <div class="admin_cafe_banner">
            <input @change="fileChoice" type="file" name="file" id="file">
            <!-- <input type="text" name="url" id="url" placeholder="   " v-model="banner_img">
            <img class="url_img" :src="banner_img" v-if="banner_img != ''"/>
            <button @click="banner_img = ''"> </button> -->
            <select class="select" v-model="banner_sel">
              <option value="novelOpt"> </option>
              <option value="eventOpt"></option>
            </select>
            <input class="codeInput" type="number" placeholder="codeNum" v-model="banner_num"/>
          </div>
        </div>
      </div>
      <!-- <div class="modal_content"> </div> -->
      <!--  -->
      <!--    (,  )-->
      <div class="cafe_modal_btn_position">
        <div>
          <span class="cafe_qna">  ?</span>
        </div>
        <button  class="cafe_modal_btn" @click="uploadBannerImg()"></button>
        <button class="cafe_modal_btn" @click="clickEvent()"></button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../axios.js";
import storage from '../../aws'

export default {
  name: "vsn_modal_universal",
  data() {
    return {
      banner_file: "",
      banner_img : "",
      banner_num : "",
    };
  },
  props: {
  },
  methods: {
    fileChoice(e){
      let file = e.target.files[0]
      this.banner_file = file;
      console.log(this.banner_file);
    },
    async uploadBannerImg() {
      var result = await storage.uploadBannerImg(this.banner_file);
      if(result == "ok") {
        this.$store.commit("wholeBanner", {
          bannerFile : await storage.uploadBannerImg(this.$store.state.banner_file)
        });
      }
    
      var newContent = {
        newNum: this.banner_num,
        newSel: this.banner_sel
      };
      console.log(newContent);
      axios.post("/api/admin/addBanner", {content : newContent})
      .then((result) => {
        if (result.data == "err") {
          alert('    .');
        } else {
          this.clickEvent();
        }
      });
    },
    //        
    clickEvent() {
      this.$emit("modal");
    },
  },
};

</script>
<style>
.cafe_modal_title_inner{
    text-align:left;
}
.cafe_modal_title{
    color:white;
    font-size: 1.3em;
}
.admin_cafe_banner {
  background-color:gray;
  border-radius: 15px;
  height: 150px;
  width: 95%;
  margin: 20px 2.5% 0px 2.5%; 
  padding: 15px 5px;
}
#file, #url{
  display: inline-block;
  width: 100%;
}
#url{
  width: 27%;
  margin: 20px 0;
}
.admin_cafe_banner > button {
  margin: 0 0 0 5px;
  border-radius: 10px;
  background-color: #2a2a2a;
  color: white;
  width: 90px;
}
.url_img{
  display: inline-block;
  width: 50%;
  height: 130px;
  position: absolute;
  top: 57px;
  right: 40px;
  border-radius: 20px;
}
input[type=file]::file-selector-button {
  width: 150px;
  height: 30px;
  background: #fff;
  border: 1px solid rgb(77,77,77);
  border-radius: 10px;
  cursor: pointer;
}
input[type=file]::file-selector-button:hover{
    background: rgb(77,77,77);
    color: #fff;
}
.cafe_modal_frame_big {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 310px;
  border-radius: 25px;
  background: #2a2a2a;
  animation-duration: 0.7s;
  animation-name: opening;
  z-index: 20;
  opacity: 1;
}
.cafe_modal_inner {
  color: white;
  position: fixed;
  width: 95%;
  margin: 0px 2.5% 0px 2.5%; 
  top: 5%; 
}
.cafe_modal_btn {
    font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, 'Helvetica Neue', 'Segoe UI', 'Apple SD Gothic Neo', 'Noto Sans KR', 'Malgun Gothic', sans-serif;
    font-weight: 500;
    position: relative;
    width: 100px;
    height: 30px;
    background-color:#2872f9;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 14px;
    border-radius: 12px;
    cursor: pointer;
    margin:5px 5px 5px 20px;
    left: 31%;
}
.cafe_modal_btn_position {
  position:relative;
  width: 90%;
  margin: 0px 5% 0px 5%; 
  top: 85%;
  transform: translateY(-50%);
}
.cafe_qna {
    color:white;
    margin-left: 38%;
}
.select{
  position: absolute;
  bottom: 20px;
  left: 20px;
}
.codeInput{
  width: 100px;
  position: absolute;
  bottom: 20px;
  left: 120px;
}
